import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE, MainColor } from '@/common/constants';
import { getMyUserInfo } from "@/common/storageFunctions";
import { USER_INFO } from "@/common/storageKeys";
/**
 * schoolLogin 登录学校官网认证
 * @description 登录学校官网认证
 * @Author: 穆兰
 * @Date: 2022/1/9
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/9
 */
export default {
    data() {
        return {
            username: '',
            password: '',
            agreest: false,
            MainColor
        };
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @event {Function()} 点击同意checkbox
         */
        onCheckBoxClick() {
            this.agreest = !this.agreest;
        },
        /**
         * @event {Function()} 输入密码的input响应事件
         * @param {Object} e 事件
         */
        onPasswordInput(e) {
            this.password = e;
        },
        /**
         * @event {Function()} 输入用户名的input响应事件
         * @param {Object} e 事件
         */
        onUsernameInput(e) {
            this.username = e;
        },
        /**
         * @description 点击用户协议文字
         */
        onUserProtocolClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-use-protocol/bugu-use-protocol"
            });
        },
        /**
         * @description 点击隐私政策文字
         */
        onPrivacyPolicyClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-privacy-policy/bugu-privacy-policy"
            });
        },
        /**
         * @description 点击登录按钮
         */
        onLoginButtonClick() {
            if (this.username && this.password) {
                if (this.agreest) {
                    this.tryToLogin(this.username, this.password);
                }
                else {
                    uni.showToast({
                        title: "请先同意用户协议和隐私政策",
                        icon: 'none'
                    });
                }
            }
            else {
                uni.showToast({
                    title: "请先输入用户名或密码",
                    icon: 'none'
                });
            }
        },
        /**
         * @function 登录湖工大教务处
         * @param username 用户名
         * @param password 密码
         * @returns
         */
        async tryToLogin(username, password) {
            uni.showLoading({
                title: '正在登陆'
            });
            try {
                let res = await request({
                    data: {
                        method: 'POST',
                        group: 'verify',
                        action: 'login',
                        data: {
                            username: username,
                            password: password
                        },
                        header: {
                            'content-type': 'application/x-www-form-urlencoded' // 默认值
                        },
                    }
                });
                uni.hideLoading();
                if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                    uni.showToast({
                        title: '认证成功',
                        icon: 'success'
                    });
                    let userInfo = await getMyUserInfo();
                    userInfo.isVerify = 1;
                    uni.setStorage({
                        key: USER_INFO,
                        data: userInfo
                    });
                    setTimeout(function () {
                        uni.navigateBack({
                            delta: 1
                        });
                    }, 1000);
                }
            }
            catch {
                uni.showToast({
                    title: '登录失败，请稍后再试',
                    icon: 'none'
                });
            }
        }
    },
};
